<template>
  <v-app v-if="isLogged">
    <Header></Header>
    <v-content>
      <v-container fluid grid-list-xl>
        <Body></Body>
      </v-container>
    </v-content>
    <Footer></Footer>
  </v-app>
</template>


<script>
  import Header from './components/Header'
  import Body from './components/Body'
  import Footer from './components/Footer'
  import axios from 'axios';
  import config from './assets/js/config';
  import {isNotNull} from "./assets/js/Utils";

  export default {
    name: 'App',
    components: {
      Header,
      Body,
      Footer
    },
    data() {
      return {
        isLogged: false
      }
    },
    created(){
      this.configAxios();
      this.dassoLogin();
    },
    methods: {
      configAxios(){
        axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
        axios.defaults.headers.common['env'] = 'WLDDMQ';
        var token = localStorage.getItem("WLDDMQ_token");
        if(isNotNull(token)) {
          axios.defaults.headers.common['authorization'] = "Bearer " + token;
        }
      },
      dassoLogin(){
        var self = this;
        var token = localStorage.getItem("WLDDMQ_token");
        var isLogout = this.$route.query.logout;
        var adminComplexId = this.$route.query.adminComplexId;
        var needDassoLogin = false;
        if(isNotNull(token)){
          axios.get(config.WLDDMQ_URL + "/version")
                  .then(function (response) {
                    if (response.status == 401) {
                      localStorage.removeItem("WLDDMQ_token");
                      needDassoLogin = true;
                      self.isLogged = false;
                    }else{
                      self.isLogged = true;
                    }
                  })
        }else if(isNotNull(adminComplexId)){
          localStorage.removeItem("WLDDMQ_token");

          var params = new URLSearchParams();
          params.append('adminComplexId', adminComplexId);

          axios.post(config.WLDDMQ_URL + "/admin/authenticate", params)
                  .then(function (response) {
                    if (response.status == 200) {
                      self.isLogged = true;
                      localStorage.setItem("WLDDMQ_token", response.data.token);
                      localStorage.setItem("WLDDMQ_das", response.data.das);
                      localStorage.setItem("WLDDMQ_name", response.data.name);
                      localStorage.setItem("WLDDMQ_email", response.data.email);
                      localStorage.setItem("WLDDMQ_isAdmin", response.data.isAdmin);
                      localStorage.setItem("WLDDMQ_id", response.data.id);

                      self.isLogged = true;
                      self.$router.push('/');
                    }else{
                      alert("Error on get token");
                    }
                  })
        }else if(isNotNull(isLogout) && isLogout == "true"){
          var clientId = config.CLIENT_ID;
          var redirectUri = encodeURI(config.CLIENT_CALLBACK);
          var dassoLogin = config.OAUTH2_SERVER + "/authorize.php?client_id=" + clientId + "&response_type=code&redirect_uri=" + redirectUri;
          window.location.replace(dassoLogin);
        }else{
          needDassoLogin = true;
          self.isLogged = false;
        }
        if(needDassoLogin){
          this.dassoLogout();
        }
      },
      dassoLogout(){
        var dassoLogout = config.OAUTH2_SERVER + "/login.php?action=logout&redirect=" + encodeURI(window.location.href + "?logout=true");
        window.location.replace(dassoLogout);
      }
    },
    watch:{
      '$route': function() {
        this.configAxios();
        this.dassoLogin();
      }
    }
  }

</script>